@font-face {
  font-family: "EarthsMightiestBold";
  src: local("EarthsMightiestBold"),
    url(./fonts/EarthsMightiestBold.otf) format("otf");
}
@font-face {
  font-family: "chName";
  src: local("chName"), url(./fonts/chName.ttf) format("ttf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "chName",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
