.charactersContainer {
  display: block;
  align-items: center;
}

.characters {
  width: 1080px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.characterImg {
  width: 330px;
  height: 350px;
}

.characterCard {
  width: 330px;
  height: 370px;
  margin: 10px;
  color: azure;
  position: relative;
  perspective: 1000px;
}

.characterCardFront,
.characterCardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s;

  border-radius: 25px;
}
.characterCardFront img {
  border-radius: 25px;
}
.characterCardBack {
  backface-visibility: hidden;
  transform: rotateY(-180deg);
  background-color: rgb(230, 19, 19);
  opacity: 0.95;
  font-size: 40px;
  font-weight: 900;
  text-align: center;
}

.characterCard:hover .characterCardFront {
  transform: rotateY(-180deg);
}
.characterCard:hover .characterCardBack {
  transform: rotateY(-360deg);
}
