.paginatorItem {
  color: #fff;
  padding: 2px;
  cursor: pointer;
}

.paginatorItem:hover {
  color: rgb(21, 192, 235);
}

.activePage {
  color: rgb(21, 192, 235);
  font-weight: bold;
}
.containerPaginator {
  text-align: center;
  align-items: center;
  display: flex;
  width: max-content;
  margin: 0 auto;
}
.containerPaginator div {
  height: 21px;
}
.containerPaginator div img {
  color: #fff;
  fill: #fff;
  width: 15px;
  height: 21px;
  cursor: pointer;
}
