.characterName {
  margin: 0 auto;
  font-weight: bold;
  font-size: 60px;
  text-align: center;
  font-family: "chName";
}

.characterInfoWrapper {
  display: flex;
  margin-top: 20px;
  align-items: center;
  text-align: center;
  justify-content: space-between;
}

.characterImgContainer img {
  width: 480px;
  height: 530px;
  border-radius: 35px;
}
.characterDescription {
  width: 550px;
  font-size: 25px;
  font-weight: 700;
  font-family: "EarthsMightiestBold";
}
.characterComicsWrapper {
  margin-top: 50px;
  padding-bottom: 80px;
}
.characterComicsTitle {
  font-weight: bold;
  font-size: 42px;
  text-align: center;
}
/* slider------------------------------ */
.characterComicsContentWrapper {
  margin-top: 80px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.characterComics {
  width: 900px;
  text-align: center;
}
.characterComicsLeft {
  width: 48px;
  height: 48px;
  position: absolute;
  top: 30%;
  left: 32px;
  cursor: pointer;
}
.characterComicsRight {
  width: 48px;
  height: 48px;
  position: absolute;
  top: 30%;
  right: 32px;
  cursor: pointer;
}

.activeComicsSlide {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

.comicsSlide {
  opacity: 0;
  transition-duration: 1s ease;
}
.comicsArrow {
  position: absolute;
}
/* --------------------------------------------- */
.comicsImg {
  width: 500px;
  margin: 0 auto;
}
.comicsImg img {
  width: 400px;
  height: 550px;
}
.comicsTitle {
  margin-top: 20px;
  margin-bottom: 15px;
  font-size: 29px;
  font-weight: bold;
}
.comicsDescription {
  font-family: "EarthsMightiestBold";
  font-weight: 700;
  font-size: 20px;
}
